import React from 'react'
import tw from 'tailwind.macro'
import styled from 'styled-components'
import { Text, Flex, Box } from 'rebass'
import Fade from 'react-reveal/Fade'
import PropTypes from 'prop-types'
import GatsbyLogo from './Logo/Gatsby.svg'

const FooterContainer = styled.footer`
  ${tw`flex flex-initial flex-col justify-center items-center p-4`};
  background: ${props => props.theme.colors.primaryDark};
  color: ${props => props.theme.colors.background};
`

const ResponsiveLogo = styled.img`
  width: 100px;
  height: 25px;
  @media (min-width: 400px) {
    width: 150px;
    height: 35px;
  }
`

const And = styled(Text)`
  height: 1rem;
  span {
    position: relative;
    top: -0.5rem;
  }
`

const Logo = ({ url, logo, alt = '' }) => (
  <Box>
    <a href={url} rel='noopener noreferrer' target='_blank'>
      <ResponsiveLogo src={logo} alt={alt} />
    </a>
  </Box>
)

Logo.propTypes = {
  url: PropTypes.string.isRequired,
  logo: PropTypes.string.isRequired,
  alt: PropTypes.string,
}

const Footer = () => (
  <FooterContainer>
    <Fade bottom>
      <span>
        <Text mb={2} pt={1} pb={1}
          style={{
            display: 'table',
            textTransform: 'uppercase',
            borderBottom: 'white 3px solid',
            marginBottom: '1rem',
          }}
        >
          Powered By
        </Text>
      </span>
      <Flex justifyContent='start' alignItems='start'>
        <Logo
          url='https://www.gatsbyjs.org/'
          logo={GatsbyLogo}
          alt='Gatsby Logo'
        />
      </Flex>
    </Fade>
  </FooterContainer>
)

export default Footer